import React from 'react';
import App, { Container } from 'next/app';
import { AppProvider } from '@shopify/polaris';
import ApolloClient from 'apollo-boost';
import { ApolloProvider } from 'react-apollo';

const client = new ApolloClient({
  fetchOptions: {
    credentials: 'include'
  }
});

class Layout extends React.Component {
  constructor() {
    super();
    this.state = { workaround: false };
  }

  componentDidMount() {
    // This happens in browser - therefore we are ready to use `window`
    this.setState({ workaround: true });
  }

  render() {
    if (!this.state.workaround) {
      return <div />;
    }
    return (
      <AppProvider
        apiKey="922b224627b67c9af7b4bd72591881f8"
        shopOrigin="https://webpackstore.myshopify.com"
      >
        <ApolloProvider client={client}>{children}</ApolloProvider>
      </AppProvider>
    );
  }
}

export default class MyApp extends App {
  render() {
    const { Component, pageProps } = this.props;
    return (
      <Layout>
        <Component {...pageProps} />
      </Layout>
    );
  }
}

